*{
  box-sizing: border-box;
}
body{
  margin: 0px;
  padding: 0px;
}
.container{
  align-items: center;
  text-align: center;

}
.navbar{
  background-color: rgb(58, 58, 56);
  color: #fff;
  display: flex;
  font-size: 20px;
  justify-content: space-between;
  align-items: center;
  height: 8vh;

}

.navbar ul{
  list-style: none;
  display: flex;
  gap: 3rem;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  margin: 20px;
  padding: 0;


}

.Link{
  text-decoration: none;
  padding: 20px;
  height: 100%;
  display: flex;
  align-items: center;
  margin: 0;
  color: #fff;
  padding: 0;
}
.navbar li{
  padding: 10px;

}

.navbar li:hover{
  background-color: rgb(155, 153, 151);
}

.UserAuthentication{
  display: flex;
  width: '100%';
  justify-content: space-around;
  margin: 35px auto;
  height: fit-content;
  /* background-color: blue; */
  
}

.formField{
  margin: 10px;
}
.formInput{
  background:none;
  color: black;
  background-color: white;
  font-size: 16px;
  border: 1px solid rgb(129, 124, 124);
  border-radius: 5px;
  padding: 10px;
  width: 100%;
  margin: 15px 0px;
}

.btn{
    margin:15px;
    width: 20vw;
    background-color: black;
    color: white;
    padding: 0px 30px 0px 30px;
    cursor: pointer;
    border: none;
    height: 45px;
    font-size: 20px;
    letter-spacing: .1rem;
    border-radius: 5px;
    
}


.signcontainer{
  background-color: rgb(243, 237, 237);
  padding: 30px;
  border-radius: 10px;
}

